body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html {
  scroll-behavior: smooth;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@include media-breakpoint-up(sm) {
  html {
    font-size: 1.2rem;
  }
}

@include media-breakpoint-up(md) {
  html {
    font-size: 1.4rem;
  }
}

@include media-breakpoint-up(lg) {
  html {
    font-size: 1.6rem;
  }
}
#leadp {
  top: 0;
  left: 0;
  background-color: #dfdfdf;
  width: 100%;
  height: 100%;
  bottom: 0;
}
#about {
  width: 100%;
  background-color: red;
  height: auto;
}
#footer {
  background-color: red;
}
#education {
  width: 100%;
  height: 100%;
  background-color: #dfdfdf;
}
#contests {
  width: 100%;
  margin: auto auto;
  background-color: #dfdfdf;
}
.projectdiv {
  display: inline-block;
}
#projects {
  width: 100%;
  height: 100%;
  background-color: red;
}
#page-ender {
  text-align: center;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 8;
  bottom: 20px;
  font-size: 30px;
}
#cardprojname {
  font-size: 21px;
  text-transform: uppercase;
}
.linker {
  font-size: 40px;
  color: #dfdfdf;
  margin: 50px;
}
.heading {
  text-shadow: 1.2px 1.3px;
}
.btn-trans {
  border: 2px solid black;
  background-color: transparent;
  padding: 10px;
  border-radius: 20px;
}
.btn-trans:hover {
  transition: 1000ms;
  background-color: white;
  color: black;
  text-decoration: none;
}
ul.timeline {
  list-style-type: none;
  position: relative;
  padding-left: 1.5rem;
  padding: 1.5rem;
  margin-bottom: 0;
}
ul.timeline:before {
  content: " ";
  background: #000;
  display: inline-block;
  position: absolute;
  left: 16px;
  width: 4px;
  height: 96%;
  z-index: 400;
  border-radius: 1rem;
}
li.timeline-item {
  margin: 10px 0;
}
.timeline-arrow {
  border-top: 0.5rem solid transparent;
  border-right: 0.5rem solid #fff;
  border-bottom: 0.5rem solid transparent;
  display: block;
  position: absolute;
  left: 2rem;
}
li.timeline-item::before {
  content: " ";
  background: #ddd;
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  border: 3px solid #fff;
  left: 11px;
  width: 14px;
  height: 14px;
  z-index: 400;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}
.educatdiv {
  bottom: 0;
}
#share {
  width: 100%;
  margin-left: auto;
  text-align: center;
}
#share a {
  width: 50px;
  height: 50px;
  display: inline-block;
  margin: 8px;
  border-radius: 50%;
  font-size: 24px;
  color: #fff;
  opacity: 0.75;
  transition: opacity 0.15s linear;
}
.carousel-caption {
  background-color: black;
  color: plum;
}
#share a:hover {
  opacity: 1;
}
#share i {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
